.App {
  font-family: sans-serif;
  text-align: center;
}
* {
  box-sizing: border-box;
  margin: 0%;
  padding: 0%;
}

.container {
  display: flex;
  padding-bottom: 0%;
  margin-bottom: 0%;
  justify-content: center;
  align-items: center;
  margin: auto;
  color: rgb(221, 221, 250);
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 102, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
  min-height: 75vh;
}
.footer {
  display: flex;
  align-self: stretch;
  flex-wrap: wrap;
  color: white;
  position: absolute;
  justify-content: center;
  align-items: center;
  margin: auto;
  bottom: 0;
  left: 0;
  right: 0;
  background: #111;
  height: 25vh;
  width: 100vw;
  padding-top: 40px;
}
textarea {
  background-color: rgb(3, 1, 39);
  background: transparent;
  color: white;
}
.heading {
  padding: 60px;
  text-align: left;
  color: white;
  font-size: 30px;
}
.heading p {
  font-size: 20px;
  font-style: italic;
}
.btn {
  margin: auto;
  align-items: center;
  padding: 2.5%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.main-content {
  max-width: 50rem;
  width: 100%;
  min-height: 400px;
  height: auto;
  padding: 18px 18px 120px 18px;
  position: relative;
  resize: none;
  box-shadow: 0 12px 48px 0px rgb(109 117 141 / 20%);
  background: transparent;
  border: 0.5px solid rgb(231 233 245);
  border-radius: 16px;
}

.main-content:focus {
  border: 0.3px solid rgb(43, 45, 56, 0.3);
  outline: none;
}

p,
.main-content {
  font-size: 18px;
  letter-spacing: 1px;
  word-spacing: 2px;
  line-height: 1.6;
}

.btn-style {
  top: 50%;
  color: white;
  border: none;
  background-color: rgb(8, 3, 78);
  border-radius: 10px;
  box-shadow: 0px 0px 2px 2px rgb(231, 231, 238);
  padding: 15px;
  margin: 2.5%;
  min-height: 30px;
  min-width: 100px;
}
.fc {
  margin-right: 10rem;
  align-self: stretch;
  flex-wrap: wrap;
}
li {
  list-style-type: none;
}
.fc a {
  text-decoration: none;
  color: white;
}
.l {
  margin-top: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}
@media screen and (max-width: 768px) {
  .container {
    flex-direction: column;
    width: 100%;
    height: 100%;
  }

  h2 {
    font-size: 28px;
  }

  p {
    font-size: 16px;
    word-spacing: normal;
    margin-bottom: 40px;
    margin-top: 15px;
  }

  .main-content {
    /*border:1px solid*/
    font-size: 16px;
    word-spacing: normal;
  }

  button {
    display: flex;
    padding: 12px 12px;
    font-size: 16px;
    margin-top: -70px;
  }

  .counter-container {
    flex-direction: column;
  }
  .footer {
    visibility: hidden;
  }
}
